.navbar{
    
    background-color: black;
    padding: 10px;
    width: 340px;
    color: white;
    display: flex;
    justify-content: center;
    padding-top:50px;
    padding-bottom:40px;
}
.navbar-logo{
    width: 50px;
    height: 50px;
    border-radius: 100%;
}
.navbar-col1{
    padding: 5px;
    background-color: blue;
    gap:0.5rem;
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.navbar-ul{
    justify-content: left;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    justify-content: left;
    width: 230px;
    padding: 10px;
    padding-left: 20px;
    background-color: white;
    border: 2px solid black;
    border-radius: 5px;
}
.navbar-li{
    margin-left: 4px;
    display: flex;
    align-items: center;
    gap:0.3rem;
    display: flex;
    justify-content: left;
    align-items: center;
}
.navbar-backbutton{
    margin-left: 150px;
}
.navbar-col2{
    margin-bottom: 20px
}
.nav-anchor{
    font-size: 25px;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    text-decoration: none;
}