.contact-container-panel1{
    padding: 20px;
    background-color: rgb(245, 245, 245);
    margin: 20px;
    
}
.contact-heading-div{

    margin:0px auto 10px auto;
    max-width: 1110px;
    height: 40px;
}
.contact-container-flex{
    display: flex;
    justify-content: space-evenly;
  
}
.contact-container{
    display: flex;
    flex-direction: column;
    
    border: 10px solid black;
    border-radius: 10px;
    max-width: 1050px;
    margin:auto;
    box-shadow: 2px 2px 5px gray;
    padding: 10px 0px;
    background-color:  rgb(5, 48, 117);
    
}
.contact-col2{
    display:flex;
    flex-direction: column;
}
.contact-col1{
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.contact-input-email-div{
    display: flex;
    justify-content: center;
}
.contact-input-email{
    width: 250px;
    height: 25px;
    font-size: 20px;
}
.contact-input-phone{
    width: 200px;
    height: 25px;
    font-size: 20px;
}
.contact-select{
    height: 28px;
}
.contact-input-number-div2{
    display: flex;
    justify-content: center;
}
.contact-panel1-para{
    padding-bottom: 20px;

}
.contact-col2{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}
.contact-col2 button{
    background-color:blueviolet;
    color: white;
    height: 25px;
    border:2px solid blueviolet;
    border-radius: 10px;
    font-size: 17px
}
.contact-col2 button:hover{

    background-color:green;
}
.contact-input-email-div{
    display: flex;
    justify-content: center;
    align-items: center;
}
.contact-input-name-icon,.contact-input-phone-icon,.contact-input-email-icon{
    font-size: 24px;
    padding: 2px;
    padding-top: 2px;
    background-color: white;
    border-bottom-left-radius: 5px;
    border-top-left-radius: 5px;
    border:1px solid black;
}
.contact-input-name-div{
    display: flex;
    justify-content: center;
    align-items: center;
}
.contact-social-media-card-image{
    width: 60px;
    height: 60px;
    border-radius: 100%;
}
.contact-social-media{
    margin: auto;
    gap: 0.7rem;
    display: flex;
}
.contact{
    padding:50px 10px 0px 10px;
}
@media (max-width:1089px){
    .contact-container{
        margin: 10px;
    }
}
@media (max-width:805px){
    .contact-container-flex{
        gap:2rem;
        flex-direction: column;
    }
    .contact-msg{
        width: 250px;
        margin: auto;
    }
   
}
@media (max-width:391px){
    .contact-input-email{
        width: 200px;
        height: 20px;
        font-size: 17px;
    }
    .contact-input-phone{
        width: 150px;
        height: 20px;
        font-size: 17px;
    }
    .contact-select{
        height: 24px;
    }
    .contact-msg{
        width: 200px;
        margin: auto;
    }
}
@media (max-width:442px){
    .contact-input-email{
        width: 160px;
        height: 20px;
        font-size: 17px;
    }
    .contact-input-phone{
        width: 120px;
        height: 20px;
        font-size: 17px;
    }
    .contact-select{
        height: 24px;
        width: 40px;
        font-size: 10px;
    }
    .contact-msg{
        width: 160px;
        margin: auto;
    }
    .contact-container-panel1{
        padding: 10px;
        background-color: rgb(245, 245, 245);
        margin: 10px;
      
    }
    .contact-container{
        border: 5px solid black;
        border-radius: 10px;
        max-width: 1050px;
        margin:10px;
        box-shadow: 2px 2px 5px gray;
        padding: 10px 0px;
        color: black;
        
    }
    .contact-input-name-icon,.contact-input-phone-icon,.contact-input-email-icon{
        font-size: 19px;
        padding: 2px;
        padding-top: 2px;
        background-color: white;
        border-bottom-left-radius: 5px;
        border-top-left-radius: 5px;
        border:1px solid black;
    }
}