.header-ul{
    justify-content: space-between;
    display: flex;
    list-style: none;
    gap:2rem;
}
.header-link-name{
    font-size: 20px;
    font-weight: 500;
}
.header-anchor{
    color:white;
    font-size: 20px;
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    gap:0.3rem;
    background-color: green;
    padding: 2px 6px 2px 6px;
    border-radius: 10px;
}
.header-anchor:hover{
    color:aqua
}
.header-container{
    padding: 5px 20px;
    margin: auto;
    max-width: 1050px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.header-navbar{
    display: none;
}
.header-col1{
    display: flex;
}
.header{
    z-index: 1000;
    position: fixed;
    top:0px;
    width: 100%;
    padding: 5px 10px;
    background-color: rgb(240, 242, 243);
}
.header-logo{
    width: 30px;
    height: 30px;
    border-radius: 100%;
}
.header-anchor{
    font-size: 15px;
}
.active{
    color:skyblue;
}
@media (max-width:875px){
    .header-ul{
        gap:1rem;
    }
}
@media (max-width:799px){
    .header-ul{
        gap:0.7rem;
    }
}
@media (max-width:777px){
    .header-container{
        flex-direction: column;
        gap:0.5rem;
    }
}
@media (max-width:585px){
    .header-container{
        flex-direction: row;
        gap:0.5rem;
    }
    .header-ul{
        display: none;
    }
    .header-navbar{
        display: block;
    }
    .header-navbar-icon{
        font-size: 30px;
    }
}
.header-navbar-comp{
    position: absolute;
    left: 0px;
    top: 0px;
}
@media (min-width:571px){
    .header-navbar-comp{
        display: none;
    }
}
