.social-media-card-contaier{
    max-width: 200px;
    border-radius:30%;
    box-shadow: 2px 2px 10px gray;
    background-color: black;
    color:white;
    padding:8px;
}
.social-media-card-logo{
    width: 160px;
    height: 160px;
    border-radius: 100%;
}
.social-media-card-contaier:hover{
    position: relative;
    bottom:2px;
    left: 2px;
}
.social-media-card-name{
    color:white;
 
}
.social-media-card-anchor{
    text-decoration: none;
}