.project-card-container{
    padding: 5px 2px 5px 2px;
    width: 300px;
    height:500px;  
    box-shadow: 2px 2px 10px gray;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    color:white;
    position: relative;
    background-color:  rgb(5, 48, 117);
}
.project-card-container:hover{
    transform: 20s;
    position: relative;
    top: 2px;
    left: 2px;
}
.project-card-button-About{
    height: 35px;
    font-size: 20px;
    background-color: blue;
    color:  white;
    width: 152px;
    border:2px solid blue;
    border-radius: 5px;
    position:absolute;
    text-decoration: none;
    bottom: 0px;
    right:0px;
}
.project-card-button-code{
    position:absolute;
    bottom: 0px;
    left: 0px;
    height: 35px;
    font-size: 20px;
    background-color: green;
    color:  white;
    width: 146px;
    text-decoration: none;
    border:2px solid green;
    border-radius: 5px;
}
.project-card-button-code:hover,.project-card-button-About:hover{
    height: 30px;
}
.project-card-button-div{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}
.project-card-img{
    width: 300px;
    height: 300px;
}



