.resume-contaier{
    margin: auto;
    max-width: 988px;
    display: flex;
    box-shadow: 2px 2px 10px gray;
    justify-content: space-evenly;
    gap:1rem;
    padding: 30px;
    background-color:  rgb(5, 48, 117);
}
.resume-heading-div{
    padding: 40px 0px;
    margin: auto;
    max-width: 1050px;
}
.resume{

    padding:60px 10px 0px 10px;
}
.resume-col2{
    margin: auto;
    display: flex;
    flex-direction: column;
    gap:2rem;
    padding: 10px;
    background-color: black;
    color: white;
    max-width: 400px;
}
.resume-image{
    max-width: 350px;
    height: 350px;
    border:2px solid red;
}
.resume-heading{
    margin: 15px;
    color:white;
    writing-mode: vertical-rl;
}
.resume-download-button{
    display: flex;
    gap:1rem;
    justify-content: center;
    align-items: center;
    padding: 20px;
    font-size: 20px;
    max-width: 350px;
    background-color: green;
    color: white;
    border: 2px solid green;
    border-radius: 10px;
    text-decoration: none;
}
.resume-view-button{
    
    display: flex;
    gap:1rem;
    justify-content: center;
    align-items: center;
    padding: 20px;
    font-size: 20px;
    max-width: 350px;
    background-color: green;
    color: white;
    border: 2px solid green;
    border-radius: 10px;
    text-decoration: none;
}

.resume-download-button:hover{
    
    border: 2px solid black;
  
}
.resume-view-button:hover{
    border: 2px solid black;
}
.resume-button-div{
    display: flex;
    justify-content: space-evenly;
}

@media (max-width:885px){
    .resume-col2{
        max-width: 300px;
    }
    .resume-image{
        max-width: 300px;
    }
}
@media (max-width:781px){
    .resume-contaier{
        flex-direction: column;
    }
    .resume-col2{
        max-width: 335px;
    }
    .resume-image{
        max-width: 350px;
    }
    .resume-heading{
        margin: 5px;
        padding: 5px;
        margin-bottom: 25px;
        color:black;
        writing-mode: horizontal-tb;
        border:2px solid black;
        background-color: aqua;
    }
}
@media (max-width:436px){
    .resume-contaier{
        flex-direction: column;
    }
    .resume-col2{
        max-width: 285px;
    }
    .resume-image{
        max-width: 300px;
    }
}
@media (max-width:385px){
    .resume-contaier{
        flex-direction: column;
    }
    .resume-col2{
        max-width: 235px;
    }
    .resume-image{
        max-width: 250px;
    }
}
@media (max-width:327px){
    .resume-contaier{
        flex-direction: column;
    }
    .resume-col2{
        max-width: 185px;
    }
    .resume-image{
        max-width: 200px;
    }
    .resume-download-button{
        width:80px;
    }
    .resume-view-button{
        width:60px;
    }
    
}
@media (max-width:781px) {
    .resume-container{
        flex-direction: row;
    }
}