.about-container{
    gap:1rem;
    padding: 20px;
    max-width: 1010px;
    margin:0px auto 0px auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    box-shadow: 2px 2px 10px gray;
    background-color:  rgb(5, 48, 117);

}
.about-heading-div{
    margin-top: 50px;
    margin: 40px auto;
    max-width: 1110px;

    height: 40px;
}
.about{
    margin: 65px auto 0px auto;
    gap: 1rem;
}
.about-para{
    max-width: 500px;
}
.about-img{
    border-radius: 100%;
    width: 250px;
    height:250px;
}
.about{
    padding: 0px 10px 0px 10px;
}
.about-about{
    color: white;
}
.about-container-col1{
    margin-top:20px;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 500px;
    border-radius: 10px;
    color: whitesmoke;
    font-size: 20px;
    border:2px solid black;

}
@media (max-width:679px){
    .about-container{
        flex-direction: column;
    
    }
    .about-container-col1{
        margin: auto;
    }
}
@media (max-width:775px){
    .about{
        margin: 90px auto 0px auto;
    }
}
@media (max-width:333px){
    .about-img{
        width: 200px;
        height:200px;
    }
}
.about-heading{
    font-size: 30px;
    color: skyblue;
}
@media (max-width:585px){
    .about{
        margin: 80px auto 0px auto;
    }
}